.Car {
  background-color: #424242;
  padding: 1rem;
  position: relative;
  border-radius: 10px;
  display: flex;
  align-items: center;
}
.Car > div:last-of-type {
  display: flex;
  justify-content: space-between;
  margin-left: 1rem;
  width: -webkit-fill-available;
  width: -moz-available;
  width: fill-available;
  align-items: center;
  font-size: 2rem;
  color: #fff;
}
.Car > div:last-of-type div {
  background-color: #370053;
  padding: 1rem 3rem;
  border-radius: 20px;
  border: 1px solid #fff;
}
.Car button {
  top: 1rem;
  right: 1rem;
  position: absolute;
}/*# sourceMappingURL=Car.css.map */